.app-wrapper {
  display: grid;
  width: 1200px;
  margin: 0 auto;

  grid-template-areas:
  'h h'
  'n c';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: c;
  background-color: grey;
}

.post-enter {
  transform: translateX(0px);
}
.post-enter-active {
transform: translateX(350px);
transition: all 500ms ease-in;
}
.post-exit {
opacity: 1;
}
.post-exit-active {
transform: translateX(-350px);
transition: all 500ms ease-in;
}





